<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
				<div class="c-bg"></div>
				<Map :params="mapParams" class="map" @change='change'></Map>
      </div>
      <div class="w-right">
        <div class="r-top">
          <ul>
            <li v-for="(item, index) in menuList" 
              @click="changMenu(item)"
              :key="index" :class="item === selectName ? 'item':''">{{item}}</li>
          </ul>
        </div>
        <div class="r-bottom">
          <ctitle :title="'信访量走势'"></ctitle>
          <div class="inner">
            <LinesR :params="zsLineParams"></LinesR>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { get_http } from "@/api/index";
  import ctitle from "@/components/title/index";
  import Map from "@/components/map/Mapyn";
  import LinesR from "@/components/line/LinesR";
  export default {
    name: 'ycxs',
    components: {
      ctitle,
      Map,
      LinesR
    },
    data() {
      return {
        shengj: '云南省',
        shij: 'invalid',
        menuList: ['全部', '城乡建设', '党务政务', '环境保护', '国土资源', '纪检监察', '交通运输', '体育文体', '经济管理', '科技信息', '劳动社保', '民政', '农村农业', '其他', '商贸旅游', '卫生计生', '政法', '组织人事'],
        selectName: '全部',
        // 信访量走势
        zsLineParams: {
          data: [],
          colors: [
            {
              line: "#1a82f7",
              start: "rgba(255, 180, 53, .5)",
              end: "rgba(255, 180, 53, .5)"
            },
            {
              line: "#e11770",
              start: "rgba(225, 23, 112, .5)",
              end: "rgba(225, 23, 112, .5)"
            },
          ],
          option: {
            dashKey: '预估',
            type: 'dotted',
            showSymbol: true,
            splitLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisYName:"",
            axisCategoryName: "",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "8%",
            right: "15%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "15%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {},
          xName: "name"
        },
        // 地图
        mapParams: {
          canvasStyle: {
            width: "100%",
            height: "98%",
          },
          data: [],
          options: {
            tooltipNotShow: true,
            borderColor: '#77608e',
            scatterColor: '#3cfff8',
            emphasisColor: '#ffd200',
            normalAreaColor: '#649dfb',
            emphasisAreaColor: '#66e8ff',
            emphasisBorderColor: '#fff',
            emphasis: {
              borderWidth: 2,
              shadowColor: '#1f4ea8',
              shadowBlur: 15,
              fontSize: 16
            },
            isHome: false,
            JSON: "530000",
            xName: "name",
						yName: "value",
						top: 0,
            map: {
              "value": "信访量"
            },
            carousel: false
          }
				},
      }
    },
    mounted() {
      this.getMapAPI()
    },
    methods: {
      change(name) {
        this.shij = name;
        this.getAPI(this.year);
      },
      changMenu(item) {
        this.selectName = item;
        this.getAPI(this.year);
      },
      getMapAPI() {
        // 1.地图
        get_http("xfxsyjycYcxsMap", {
          shengj: this.shengj,
          
        })
        .then(res => {
          this.mapParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
        this.getAPI(this.year);
      
      },
      getAPI(year) {
        // 2.信访量走势
        get_http("xfxsyjycYcxsZsLine", {
          year: year,
          shengj: this.shengj,
          shij: this.shij
        })
        .then(res => {
          this.zsLineParams.map = res.data.map;
          this.zsLineParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val);
      }
    }
  }
</script>

<style lang="stylus" scoped>
.wrapper
  position relative
  top 0.45rem
  height calc(100% - 45px)
  .content
    width 100%
    height 100%
    display flex
    padding .2rem .3rem
    box-sizing border-box
    .w-left
      position relative
      flex 1
      height 100%
      display flex
      flex-direction column
      .map-chart
        background none
        position absolute
        top -45px
      .c-bg
        position absolute
        width 100%
        height 100%
        z-index 0
        top 0
        left 0
        background url("../../../assets/map-bg.png") no-repeat center bottom
    .w-right
      flex 1
      display flex
      flex-direction column
      .r-top
        flex 1
        display flex
        align-items center
        text-align center
        ul
          display flex
          flex-wrap wrap
          li
            border 1px solid #29adff
            color #8bf5ff
            font-weight 500
            text-shadow 0 0 5px #39676f
            width 14%
            border-radius 0.3rem
            background-color rgba(0, 0, 0, 0.502)
            margin 11px 13px
            line-height 0.3rem
            text-align center
            height 0.32rem
            font-size 0.16rem
            cursor pointer
          .item
            color #fff
            font-weight 700
            text-shadow 0 0 10px #fff
            position relative
            &:after
              content ''
              background-color rgb(243, 255, 58)
              position absolute
              width 70%
              height 0.02rem
              top 100%
              left 15%
      .r-bottom
        flex 1
        .inner
          width 100%
          height 100%
</style>
